import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';

import translationEN from '../locales/en.json';
import translationZH from '../locales/zh.json';
import translationJP from '../locales/jp.json';

// ËÆæÁΩÆÊîØÊåÅÁöÑËØ≠Ë®ÄÂàóË°®
export const supportedLanguages = ['zh', 'en', 'jp'] as const;
export type SupportedLanguage = typeof supportedLanguages[number];

// Ëé∑ÂèñÁî®Êà∑È¶ñÈÄâËØ≠Ë®Ä
const getDefaultLanguage = (): SupportedLanguage => {
  console.log('üîç ËØ≠Ë®ÄÊ£ÄÊµãÂºÄÂßã...');
  
  // ‰ºòÂÖà‰ªéÊú¨Âú∞Â≠òÂÇ®Ëé∑Âèñ
  const savedLanguage = localStorage.getItem('language') as SupportedLanguage;
  console.log('üì± Êú¨Âú∞Â≠òÂÇ®ÁöÑËØ≠Ë®Ä:', savedLanguage);
  if (savedLanguage && supportedLanguages.includes(savedLanguage)) {
    console.log('‚úÖ ‰ΩøÁî®Êú¨Âú∞Â≠òÂÇ®ÁöÑËØ≠Ë®Ä:', savedLanguage);
    return savedLanguage;
  }
  
  // Áõ¥Êé•ËøîÂõûÈªòËÆ§‰∏≠ÊñáÔºå‰∏çÊ£ÄÊü•ÊµèËßàÂô®ËØ≠Ë®Ä
  console.log('‚ö†Ô∏è ÂøΩÁï•ÊµèËßàÂô®ËØ≠Ë®ÄËÆæÁΩÆÔºåÁõ¥Êé•‰ΩøÁî®ÈªòËÆ§‰∏≠Êñá zh');
  return 'zh';
};

// ÂàùÂßãÂåñ i18n
export const initI18n = (lang?: SupportedLanguage) => {
  const language = lang || getDefaultLanguage();
  console.log('üöÄ ÂàùÂßãÂåñ i18nÔºåÊúÄÁªà‰ΩøÁî®ËØ≠Ë®Ä:', language);
  
  void i18n.use(initReactI18next).init({
    resources: {
      en: {
        translation: translationEN,
      },
      zh: {
        translation: translationZH,
      },
      jp: {
        translation: translationJP,
      },
    },
    lng: language,
    fallbackLng: 'zh',
    interpolation: {
      escapeValue: false,
    },
    detection: {
      order: ['localStorage', 'navigator'],
    },
  });
  
  return i18n;
};

// ÂàáÊç¢ËØ≠Ë®Ä
export const changeLanguage = (lang: SupportedLanguage) => {
  localStorage.setItem('language', lang);
  void i18n.changeLanguage(lang);
};

// Ëé∑ÂèñÂΩìÂâçËØ≠Ë®Ä
export const getCurrentLanguage = (): SupportedLanguage => {
  return (i18n.language as SupportedLanguage) || 'zh';
};

// ÂØºÂá∫ÈªòËÆ§ÂÆû‰æã
export default i18n; 